"use client";
import { useState, useEffect } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useRouter } from "next/navigation";
import { Spacer } from "@/components/ui/spacer";
import Image from "next/image";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Sparkles } from "lucide-react";

// Component to display a single history item
const HistoryItem = ({ imageUrl, summaryData }) => {
  return (
    <Card className="mb-4 overflow-hidden">
      <CardContent className="p-4">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="w-full md:w-1/3 h-64 relative">
            <Image
              src={imageUrl}
              alt="Uploaded image"
              fill
              className="object-contain rounded-md"
            />
          </div>
          <div className="w-full md:w-2/3">
            <h3 className="text-lg font-bold mb-2">
              {summaryData.title || "Untitled"}
            </h3>
            <p className="text-sm text-gray-600 mb-2">
              Created:{" "}
              {new Date(summaryData.timestamp || Date.now()).toLocaleString()}
            </p>

            {/* Original JSON data display */}
            <div className="bg-gray-100 p-3 rounded-md mb-3">
              <p className="font-medium text-sm text-gray-700 mb-1">Details:</p>
              <p>
                {summaryData.description ||
                  summaryData.details ||
                  JSON.stringify(summaryData)}
              </p>
            </div>

            {/* Gemini-generated summary */}
            {summaryData.aiSummary && (
              <div className="bg-blue-50 p-3 rounded-md flex">
                <Sparkles className="text-blue-500 mr-2 h-5 w-5 mt-1 flex-shrink-0" />
                <div>
                  <p className="font-medium text-sm text-blue-700 mb-1">
                    AI Summary:
                  </p>
                  <p className="text-blue-800">{summaryData.aiSummary}</p>
                </div>
              </div>
            )}

            {/* Tags display */}
            {summaryData.tags && summaryData.tags.length > 0 && (
              <div className="mt-3 flex flex-wrap gap-2">
                {summaryData.tags.map((tag, index) => (
                  <span
                    key={index}
                    className="px-2 py-1 bg-blue-100 text-blue-800 text-xs rounded-full"
                  >
                    {tag}
                  </span>
                ))}
              </div>
            )}
          </div>
        </div>
      </CardContent>
      <CardFooter className="bg-gray-50 px-4 py-2 flex justify-end">
        <Button variant="outline" size="sm">
          View Details
        </Button>
      </CardFooter>
    </Card>
  );
};

export default function HistoryPage() {
  const router = useRouter();
  const [historyItems, setHistoryItems] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchHistoryItems = async () => {
      try {
        setIsLoading(true);

        // Fetch the list of folders from the saved directory
        const response = await fetch("/api/history");

        if (!response.ok) {
          throw new Error("Failed to fetch history items");
        }

        const data = await response.json();
        setHistoryItems(data.items);
      } catch (err) {
        console.error("Error fetching history:", err);
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    };

    fetchHistoryItems();
  }, []);

  return (
    <div className="container mx-auto py-6">
      <Tabs
        defaultValue="history"
        onValueChange={(value) => {
          if (value === "upload") router.push("/upload");
        }}
      >
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="history">View History</TabsTrigger>
          <TabsTrigger value="upload">Upload Images</TabsTrigger>
        </TabsList>

        <TabsContent value="history" className="mt-6">
          <h2 className="text-2xl font-bold mb-2">Prompt History</h2>
          <p className="text-gray-600 mb-6">
            View your previous prompts and responses from the chatbot.
          </p>

          {isLoading ? (
            <div className="flex justify-center py-12">
              <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
            </div>
          ) : error ? (
            <div className="bg-red-50 border border-red-200 rounded-md p-4 text-red-600">
              {error}
            </div>
          ) : historyItems.length === 0 ? (
            <div className="text-center py-12 text-gray-500">
              No history available yet...
            </div>
          ) : (
            <div className="space-y-6">
              {historyItems.map((item, index) => (
                <HistoryItem
                  key={index}
                  imageUrl={item.imageUrl}
                  summaryData={item.summary}
                />
              ))}
            </div>
          )}

          <Spacer />
          <footer className="text-center text-sm text-gray-500 mt-8">
            Powered by NeuroAccess
          </footer>
        </TabsContent>
      </Tabs>
    </div>
  );
}
